import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AboutusComponent } from './components/aboutus/aboutus.component';
import { HomeComponent } from './components/home/home.component';
import { AllproductsComponent } from './products/allproducts/allproducts.component';
import { LedBulbComponent } from './products/led-bulb/led-bulb.component';
import { DealernetworksComponent } from './dealer/dealernetworks/dealernetworks.component';
import { BecomedealerComponent } from './dealer/becomedealer/becomedealer.component';
import { FaqComponent } from './faq/faq.component';
import { EnquiryComponent } from './enquiry/enquiry.component';
import { ContactusComponent } from './components/contactus/contactus.component';
import { OurqualityComponent } from './ourquality/ourquality.component';
import { VisiomComponent } from './visiom/visiom.component';
import { InverterledbulbComponent } from './products/inverterledbulb/inverterledbulb.component';


const routes: Routes = [
  {path:'about',component:AboutusComponent},
  {path:'home',component:HomeComponent},
  {path:'our-all-products',component:AllproductsComponent},
  {path:'orrbi-led-bulb',component:LedBulbComponent},
  {path:'our-dealer-networks',component:DealernetworksComponent},
  {path:'become-dealer',component:BecomedealerComponent},
  {path:'frequently-asked-questions',component:FaqComponent},
  {path:'enquiry',component:EnquiryComponent},
  {path:'contact-us',component:ContactusComponent},
  {path:'our-quality',component:OurqualityComponent},
  {path:'about-vision-mission',component:VisiomComponent},
  {path:'inverter-led-bulb',component:InverterledbulbComponent}
];


@NgModule({
  imports: [RouterModule.forRoot(routes, {scrollOffset: [0, 0], scrollPositionRestoration: 'top' })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
